/*--------start globales------*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #EBE7DE;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, .h2White, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .h2White {
  font-size: 3.6rem;
  color: #5E311E;
}
h2 span, .h2White span {
  color: #C39C63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .h2White::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.h2White {
  color: #ffffff;
}
.h2White span {
  color: #ffffff;
}
.h2White::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

/*----------header----------*/
.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
@media (min-width: 480px) {
  .header {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
.header::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contentHeader {
  position: relative;
}
.header .contentHeader h1 {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contentHeader h1 {
    margin-top: 10rem;
  }
}

/*img header de cada pagina*/
.headerInicio {
  background-image: url(../img/header_inicio.jpg);
}

.headerWe {
  background-image: url(../img/header_nosotros.jpg);
}

.headerProceso {
  background-image: url(../img/header_proceso.jpg);
}

.headerMenu {
  background-image: url(../img/header_menu.jpg);
}

.headerGallery {
  background-image: url(../img/header_galeria.jpg);
}

.headerContact {
  background-image: url(../img/header_contacto.jpg);
}

/*----------navigation----------*/
.navPrincipal {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navPrincipal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.navPrincipal a {
  text-align: center;
  color: #ffffff;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navPrincipal a {
    margin-bottom: 0;
  }
}
.navPrincipal a:hover {
  color: #C39C63;
}
.navPrincipal a.activo {
  color: #C39C63;
}

/*----------barra----------*/
.bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.bar .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar .logo {
    width: 20rem;
    margin: 0;
  }
}

.footer {
  background-color: #5E311E;
}
.footer .ContentFooter {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .ContentFooter {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyRight {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #ffffff;
}

.footerBox {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footerBox {
    margin-bottom: 0;
  }
}
.footerBox h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #C39C63;
}
.footerBox p, .footerBox a {
  font-size: 2rem;
  margin: 0;
}

.contentPrincipal {
  background-color: #ffffff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contentPrincipal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons .iconsList {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.icons .iconsList li img {
  margin: 0 auto;
  width: 12rem;
}
.icons .iconsList li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5E311E;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery .image img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .gallery .image:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery .image:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .gallery .image:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery .image:nth-child(2) img {
    width: 62rem;
  }
  .gallery .image:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery .image:nth-child(4) img {
    height: 62rem;
  }
  .gallery .image:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(5) img {
    width: 62rem;
  }
  .gallery .image:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(6) img {
    width: 62rem;
  }
  .gallery .image:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery .image:nth-child(7) img {
    height: 62rem;
  }
  .gallery .image:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery .image:nth-child(8) img {
    width: 94rem;
  }
  .gallery .image:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery .image:nth-child(9) img {
    width: 62rem;
  }
  .gallery .image:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(10) img {
    width: 62rem;
  }
}

.ourMenu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5E311E, #5E311E);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .ourMenu {
    background-size: 100%;
    padding: 15rem 0 20rem 0;
  }
}
@media (min-width: 1024px) {
  .ourMenu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .gridMenu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: #ffffff;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1024px) {
  .menu li {
    padding: 0 5rem;
  }
}
.menu li p {
  color: #ffffff;
  font-size: 1.7rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #C39C63;
}
.menu li .precio {
  font-size: 3.7rem;
  font-weight: 700;
}

.ourTestimonials {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .ourTestimonials {
    padding: 10rem;
  }
}
.ourTestimonials .testimonials {
  background-color: #EBE7DE;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
}
.ourTestimonials .testimonials .author {
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Bona Nova", serif;
}

@media (min-width: 768px) {
  .contentWe {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contentWe .image {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contentWe .image {
    margin: 0 3rem 0 0;
  }
}
.contentWe .image img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.contentWe .text blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5E311E;
  font-size: 2rem;
}

.coffeeProcess {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .coffeeProcess {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.coffeeProcess:last-of-type {
  margin-bottom: 0;
}
.coffeeProcess:nth-child(even) .text {
  order: -1;
}
.coffeeProcess .image, .coffeeProcess .text {
  flex: 1rem;
}
.coffeeProcess .text {
  margin-top: 1rem;
}
.coffeeProcess .text h3 {
  margin: 0;
  text-align: left;
  color: #5E311E;
}
.coffeeProcess .text h3::after {
  content: "";
  display: block;
  width: 35%;
  height: 2px;
  background-color: #C39C63;
}

.contentMenu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .contentMenu {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .contentMenu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contentMenu .text {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contentContact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: center right;
    background-size: 40%;
  }
}

.formContact .field {
  margin-bottom: 2rem;
}
.formContact .field:last-of-type {
  margin-bottom: 0;
}
.formContact .field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.formContact .field input:not([type=submit]), .formContact .field select {
  width: 100%;
  padding: 1rem;
  border: 0.4rem solid #5E311E;
}
.formContact .field input[type=submit] {
  background-color: #5E311E;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #ffffff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.formContact .field input[type=submit]:hover {
  background-color: #8c320d;
  cursor: pointer;
}

/*----------utilities----------*/
.textCenter {
  text-align: center;
}

.bgPrimary {
  background-color: #5E311E;
}

.bgWhite {
  background-color: #ffffff;
}

.marginNegative-10 {
  margin-top: -10rem;
}

.paddingBottom-0 {
  padding-bottom: 0;
}