/*----------navigation----------*/

@use 'base/variables' as v;
@use 'base/mixins' as m;

.navPrincipal {
    display: flex;
    flex-direction: column;
    margin-top: 5rem;

    @include m.tablet { 
        flex-direction: row;
        gap: 1rem;
        margin-top: 0;
     }

    a {
        text-align: center;
        color: v.$white;
        font-size: 1.5rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;
        @include m.tablet { 
            margin-bottom: 0;
        }

        &:hover {
            color: v.$secondary;
        }

        &.activo {
            color: v.$secondary;
        }
        
    }
}