@use 'base/variables' as v;
@use 'base/mixins' as m;

.ourMenu {
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, v.$primary, v.$primary);
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, 100%;
    background-size: 200%;
    padding: 15rem 2rem 20rem 2rem;

    @include m.tablet {
        background-size: 100%;
        padding: 15rem 0 20rem 0;
    }

    @include m.desktop { 
        background-size: 50%, 50%, 100%;
     }
}

.gridMenu{
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
}

.menu {
    h3 {
        color: v.$white;
    }

    li {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include m.desktop { 
            padding: 0 5rem;
         }

        p{
            color: v.$white;
            font-size: 1.7rem;
            line-height: 1.5;
            margin: 0 0 2rem 0;

            span {
                display: block;
                color: v.$secondary;
            }
        }

        .precio {
            font-size: 3.7rem;
            font-weight: 700;
        }
    }

}